import React, { Component } from 'react';

import './order_history.scss';
import axios from 'axios';

class order_history extends Component {
	constructor(props) {
		super(props);
		this.state = {
			orders: []
		};
	}

	componentWillMount() {
		axios
			.get('https://medicento-api.herokuapp.com/api/get_orders')
			.then((response) => {
				console.log(response);
				this.setState({ orders: response.data.orders });
			})
			.catch((err) => {});
	}

	render() {
		const orders = this.state.orders;

		return (
			<div class="container order_history">
				<h3 class="d-flex justify-content-center">All Orders</h3>
				<table class="tableData" class="table table-striped table-bordered">
					<thead>
						<th>Order Code</th>
						<th>Retailer</th>
						<th>Amount</th>
						<th>Date</th>
						<th>Details</th>
						<th>State</th>
					</thead>
					<tbody>
						{orders.map((order) => (
							<tr>
								<td>{order.sales_order_code}</td>
								<td>{order.pharmacy_id.pharma_name}</td>
								<td>{order.grand_total}</td>
								<td>{order.created_at}</td>
								<td>
									{order.order_items.map((order_item) => (
										<div style={{ fontSize: '10px' }}>
											{order_item.medicento_name}{' '}
											<span class="badge badge-light">{order_item.quantity}</span>
										</div>
									))}
								</td>
								<td>Karnataka</td>
							</tr>
						))}
					</tbody>
				</table>
			</div>
		);
	}
}
export default order_history;
