import React, { Component } from 'react';

import './main.scss';
import axios from 'axios';

class main extends Component {
	constructor(props) {
		super(props);
		this.state = {
			total_orders: 0,
			orders: [],
			active_orders: 0,
			cancel_orders: 0,
			delivered_orders: 0,
			un_delivered_orders: 6,
			returned_orders: 0
		};
	}

	componentWillMount() {
		axios
			.get('https://medicento-api.herokuapp.com/api/get_order_count')
			.then((response) => {
				console.log(response);
				this.setState({
					total_orders: response.data.total_orders,
					active_orders: response.data.active_orders,
					cancel_orders: response.data.cancel_orders,
					delivered_orders: response.data.delivered_orders,
					returned_orders: response.data.returned_orders,
					un_delivered_orders: response.data.un_delivered_orders
				});
				axios
					.get('https://medicento-api.herokuapp.com/api/get_orders')
					.then((response) => {
						console.log(response);
						this.setState({ orders: response.data.orders });
					})
					.catch((err) => {});
			})
			.catch((err) => {});
	}

	render() {
		const total_orders = this.state.total_orders;
		const active_orders = this.state.active_orders;
		const cancel_orders = this.state.cancel_orders;
		const delivered_orders = this.state.delivered_orders;
		const un_delivered_orders = this.state.un_delivered_orders;
		const returned_orders = this.state.returned_orders;
		const orders = this.state.orders;

		return (
			<div className="main">
				<div class="row">
					<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
						<div class="card bg-primary" style={{ borderLeft: '5px solid black;', cursor: 'pointer;' }}>
							<div class="card-body">
								<div class="card-title">Orders</div>
								<div class="card-text">
									<span class="fa fa-angle-double-right" />
									{' ' + total_orders}
								</div>
							</div>
						</div>
					</div>
					<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
						<div
							class="card bg-info"
							style={{ borderLeft: '5px solid black;', cursor: 'pointer;' }}
							onclick="showA()"
						>
							<div class="card-body">
								<div class="card-title">Orders Active</div>
								<div class="card-text">
									<span class="fa fa-angle-double-up" />
									{' ' + active_orders}
								</div>
							</div>
						</div>
					</div>
					<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
						<div
							class="card bg-dark"
							style={{ borderLeft: '5px solid black;', cursor: 'pointer;' }}
							onclick="showC()"
						>
							<div class="card-body">
								<div class="card-title">Orders Canceled</div>
								<div class="card-text">
									<span class="fa fa-angle-double-down" />
									{' ' + cancel_orders}
								</div>
							</div>
						</div>
					</div>
					<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
						<div class="card bg-danger" style={{ borderLeft: '5px solid black;', cursor: 'pointer;' }}>
							<div class="card-body">
								<div class="card-title">Orders Returns</div>
								<div class="card-text">
									<span class="fa fa-angle-double-left" />
									{' ' + returned_orders}
								</div>
							</div>
						</div>
					</div>
					<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
						<div
							class="card bg-success"
							style={{ borderLeft: '5px solid black;', cursor: 'pointer;' }}
							onclick="showD()"
						>
							<div class="card-body">
								<div class="card-title">Orders Delivered</div>
								<div class="card-text">
									<span class="fa fa-angle-double-up" />
									{' ' + delivered_orders}
								</div>
							</div>
						</div>
					</div>
					<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
						<div
							class="card bg-secondary"
							style={{ borderLeft: '5px solid black;', cursor: 'pointer;' }}
							onclick="showA()"
						>
							<div class="card-body">
								<div class="card-title">Orders Un Delivered</div>
								<div class="card-text">
									<span class="fa fa-angle-double-up" />
									{' ' + un_delivered_orders}
								</div>
							</div>
						</div>
					</div>
					<div class="container">
						<h3>Active Orders</h3>
						<table class="tableData" class="table table-striped table-bordered">
							<thead>
								<th>Order Code</th>
								<th>Retailer</th>
								<th>Amount</th>
								<th>Date</th>
								<th>Details</th>
								<th>State</th>
							</thead>
							<tbody>
								{orders.map((order) => (
									<tr>
										<td>{order.sales_order_code}</td>
										<td>{order.pharmacy_id.pharma_name}</td>
										<td>{order.grand_total}</td>
										<td>{order.created_at}</td>
										<td>
											{order.order_items.map((order_item) => (
												<div style={{ fontSize: '10px' }}>
													{order_item.medicento_name}{' '}
													<span class="badge badge-light">{order_item.quantity}</span>
												</div>
											))}
										</td>
										<td>Karnataka</td>
									</tr>
								))}
							</tbody>
						</table>
					</div>
				</div>
			</div>
		);
	}
}

export default main;
