import React, { Component } from 'react';

import './area.scss';

import axios from 'axios';

class area extends Component {
	constructor(props) {
		super(props);
		this.state = {
			areas: []
		};
	}

	componentWillMount() {
		axios
			.get('https://medicento-api.herokuapp.com/api/get_areas_by_city')
			.then((response) => {
				console.log(response);
				this.setState({ areas: response.data.areas });
			})
			.catch((err) => {
				console.log(err);
			});
	}

	render() {
		const areas = this.state.areas;

		return (
			<div className="area">
				<h2 class="bg-primary text-center text-white p-3">Medicento Areas List</h2>
				<section class="container">
					<div class="search_area row my-3 mx-0">
						<div class="state mr-3">
							<select name="state" id="state" onchange="change_city_dropdown(event)">
								<option value="" selected disabled>
									Select State
								</option>
								<option value="">Karnataka</option>
							</select>
						</div>
						<div class="city mr-3 ">
							<select name="city" id="city" onchange="change_area_dropdown(event)">
								<option value="" selected disabled>
									Select City
								</option>
								<option value="">Bangalore</option>
							</select>
						</div>
						<div class="input-group mt-2">
							<input
								type="text"
								name="area_name"
								id="area_name"
								placeholder="Enter Area"
								class="form-control"
							/>
							<input
								type="text"
								name="area_pincode"
								id="area_pincode"
								placeholder="Enter Pincode"
								class="form-control"
							/>
							<button class="btn btn-success input-group-append" onclick="addNewArea()">
								Add New Area
							</button>
						</div>
					</div>
					<table className="table table-bordered">
						<thead>
							<tr>
								<th>Id</th>
								<th>Area</th>
								<th>Pincode</th>
								<th>Edit</th>
							</tr>
						</thead>
						<tbody>
							{areas.map((area) => (
								<tr>
									<td>{area._id}</td>
									<td>{area.area_name}</td>
									<td>{area.area_pincode}</td>
									<td>
										<button className="btn btn-primary">Edit</button>
									</td>
								</tr>
							))}
						</tbody>
					</table>
				</section>
			</div>
		);
	}
}

export default area;
